import {schemaEmitter} from "@/main";

export default {
    data() {
        return {
            cols: 12,
            classes: ''
        }
    },
    mounted() {
        if (this.node.styles.cols !== undefined) {
            this.cols = this.node.styles.cols
        }
        if( this.node.styles.classes !== undefined ) {
            this.classes = this.node.styles.classes
        }
    }
}
