<template>
  <div>
    <v-select
        v-model="selected"
        :items="componentProps.value"
        item-text="value"
        item-value="value"
        attach=""
        :label="componentProps.placeholder"
        @change="updateField(updateKey, getSelectedObject($event, componentProps.value))"
        v-bind="vuetifyProps"
    ></v-select>
  </div>
</template>

<script>

import {mapGetters, mapActions, mapMutations} from 'vuex'
import {schemaEmitter} from "@/main";
import Schema from "@//components/Schema"
import formProps from '@/mixins/form-props'
import functions from "@/mixins/functions";

export default {
  mixins: [formProps, functions],
  props: ['name', 'placeholder', 'fieldValues', 'validation', 'updateKey', 'schemas_type'],
  components: {
    Schema
  },
  data() {
    return {
      selected: '',
      dropdown_texts: [],
      dropdown_values: [],
      schemas: this.schemas_type,
      result: '',
    }
  },
  methods: {
    ...mapActions(['run']),
    ...mapMutations(['updateProperty']),
    updateField(field, value) {
      schemaEmitter.$emit('input', field, {
        value: value
      })
      this.changeView()
    },
    changeView() {
      if (!this.getSchemaType.hasOwnProperty('offers')) {
        return
      }
      if ('undefined' === typeof this.componentProps.changeView || false === this.componentProps.changeView) {
        return
      }
      let offer = this.getSchemaType.offers.children
      for (const property in this.getSchemaType.offers.children) {
        for (const i in offer[property]) {
          if (i === "showOnSelect") {
            if (this.selected === offer[property][i]['value']) {
              schemaEmitter.$emit('input', property, {
                render: true,
                renderJson: offer[property][i]['show']['renderJson']
              })
            } else {
              schemaEmitter.$emit('input', property, {
                render: false,
                renderJson: offer[property][i]['hide']['renderJson'],
                value: this.setEmptyValue(offer[property]['value'])
              })
            }
          }
        }
      }
      this.updateProperty(['schemaType', this.getSchemaType])
    },
    getSelectedObject(value, fields) {
      let index = fields.findIndex((i) => {
        return value === i.value
      })
      for (let key in fields) {
        if (parseInt(key) === index) {
          fields[key].selected = true
        } else {
          fields[key].selected = false
        }
      }

      return fields
    },
    getSelectedValue() {
      let selected_index = this.componentProps.value.findIndex(function (i) {
        return i.selected
      })
      return this.componentProps.value[selected_index]
    },
    getDropDownText() {
      return this.componentProps.value.map(function (item) {
        return item.value
      })
    },
    getDropDownValues() {
      let result = []
      let keys = this.componentProps.value.keys()
      for (let key of keys) {
        result.push(key)
      }

      return result
    }
  },
  validations() {
    return {
      selected: this.validators
    }
  },
  mounted() {
    this.selected = this.getSelectedValue()
    // this.dropdown_texts = this.getDropDownText()
    // this.dropdown_values = this.getDropDownValues()
  },
  computed: {
    ...mapGetters(["getJsonType", "getSchemaType", "getSourceType"]),
    getSelectedItem: function () {
      return this.componentProps.value.findIndex(function (i) {
        return i.selected
      })
    }
  },
  // watch: {
  //   selected: {
  //     handler: 'changeView'
  //   },
  // },

}
</script>

