export default {
    state: {
        currentSchema: {},
        schemasList: {},
    },
    mutations: {
        updateCurrentSchema(state, value) {
            state.currentSchema = value
        },
        updateSchemasList(state, value) {
            state.schemasList = value
        }
    },
    actions: {
        setCurrentSchema: function ({commit}, data) {
            commit('updateCurrentSchema', data)
        },
        setSchemasList: function ({commit}, data) {
            commit('updateSchemasList', data)
        }
    },
    getters: {
        getCurrentSchema(state) {
            return state.currentSchema;
        },
        getSchemasList(state) {
            return state.schemasList
        }
    }

}