<template>
  <div>
    <v-text-field
        v-model="getCurrentValue"
        v-if="componentProps.render"
        :label="componentProps.placeholder"
        @blur="validateInput($v)"
        @input="updateField(updateKey, $event)"
        :counter="counter"
        :counter-value="printCounterValue"
        :error-messages="displayErrors(componentProps.validation, $v.getCurrentValue)"
        v-bind="vuetifyProps"
    ></v-text-field>
  </div>
</template>

<script>

import validation from "@/mixins/validation";
import formProps from "@/mixins/form-props";
import rating from "@/mixins/rating";
import {mapGetters} from 'vuex'
import {schemaEmitter} from "@/main";

export default {
  mixins: [validation, formProps, rating],
  props: {
    updateKey: {
      type: [String, Array, Number],
      required: true
    }
  },
  data() {
    return {
      currentValue: '',
    }
  },
  methods: {
    ...mapGetters(['getSchemaType', 'getReviews']),
    updateField(field, value) {
      this.validateInput(this.$v)
      this.calculateRating(field, value)
      schemaEmitter.$emit('input', field, value)
      this.setRatingValues(field, value)
      this.calculateValue()
      this.duplicateValues(value)
    },
    setRatingValues(field, value) {
      let reviewsKeys = Object.keys(this.getReviews())
      reviewsKeys.forEach((key) => {
        if ('aggregateRating.bestRating' === field) {
          schemaEmitter.$emit('input', `review.${key}.reviewRating.bestRating`, value)
          schemaEmitter.$emit('input', `review.${key}.reviewRating.ratingValue`, {
            validation: {
              maxValue: value
            }
          })
        }
        if ('aggregateRating.worstRating' === field) {
          schemaEmitter.$emit('input', `review.${key}.reviewRating.worstRating`, value)
          schemaEmitter.$emit('input', `review.${key}.reviewRating.ratingValue`, {
            validation: {
              minValue: value
            }
          })
        }
      })
    },
    calculateValue() {
      if (this.componentProps.hasOwnProperty('calculateValue')) {
        let total = 0;
        let source = this.getSchemaType()
        let objectList = this.componentProps.calculateValue
        for (let item in objectList.from) {
          if (objectList.from.hasOwnProperty(item)) {
            let object = this.findVal(source, objectList.from[item])
            if ('' !== object.value) {
              total += parseInt(object.value)
            }
          }
          for (let item in objectList.to) {
            if (objectList.to.hasOwnProperty(item)) {
              let r = total > 0 ? total : ''
              schemaEmitter.$emit('input', objectList.to[item], r)
            }
          }
        }
      }
    }
  },
  mounted() {
    this.calculateRating(this.updateKey, this.getCurrentValue)
  },
  validations() {
    return {
      getCurrentValue: this.validators
    }
  },
  computed: {
    validators: {
      get() {
        return this.createValidatorsObject(this.componentProps.validation)
      },
      set(value) {
        return value
      }
    },
    getCurrentValue: {
      get() {
        return this.componentProps.value ?? 'adasdasd'
      },
      set(value) {
        return value
      }
    }
  }
}
</script>

