<template>
  <v-main>
    <v-container>
      <v-row>
        <v-col>
          <v-card>
            <v-card-text>
              <div>
                <v-select
                    v-model="getSelectedSchema"
                    :items="getSchemasList"
                    attach
                    label="Which Schema.org markup would you like to create?"
                    @change="selectSchema($event)"
                >
                  <template slot="selection" slot-scope="data">
                    <v-list-item>
                      <v-list-item-content>
                        <v-list-item-title>
                          <v-icon small>{{ data.item.icon }}</v-icon>
                          {{ data.item.title }}
                        </v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>

                  </template>
                  <template slot="item" slot-scope="data">
                    <v-icon small>{{ data.item.icon }}</v-icon>
                    <v-list-item>
                      <v-list-item-content>
                        <v-list-item-title>
                          {{ data.item.title }} <small>{{ data.item.description }}</small>
                        </v-list-item-title>
                      </v-list-item-content>
                    </v-list-item>
                  </template>
                </v-select>
              </div>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="7">
          <v-card>
            <v-card-text>
              <form-builder :config="getSchemaType"></form-builder>
            </v-card-text>
          </v-card>
        </v-col>
        <v-col cols="5">
          <v-card
              height="100%"
          >
            <v-card-text>
              <json-builder :config="getJsonType" ref="jsonTemplate"></json-builder>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <v-col
        >
          <v-card>
            <v-card-title>Select schemas conditions</v-card-title>
            <v-card-text>
              <conditions :conditionsList="conditions" :currentConditions="postConditions"></conditions>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
      <v-row>
        <input type="hidden" name="schema" v-model="result">
        <input type="hidden" name="source" v-model="source">
        <input type="hidden" name="schema_name" v-model="getSchemaName">
      </v-row>
    </v-container>
  </v-main>
</template>

<script>

import config from "@/mixins/config";
import functions from "@/mixins/functions";
import axios from 'axios'
import {mapGetters, mapActions, mapMutations} from 'vuex'
import FormBuilder from "./formBuilder";
import JsonBuilder from "./jsonBuilder";
import Conditions from "@/CondtionsElements/Conditions"
import {schemaEmitter} from "@/main";


export default {
  mixins: [config, functions],
  components: {
    FormBuilder,
    JsonBuilder,
    Conditions
  },
  props: {
    schemas_type: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      conditions: [],
      postConditions: [],
      componentKey: 0,
      schemaExist: false,
      schemaType: {},
      jsonType: {},
      sourceType: {},
      result: '',
      source: '',
      default: ''
    }
  },
  methods: {
    ...mapActions(['setDefaultData', 'setCurrentSchema', 'setSchemasList', 'run']),
    ...mapMutations(['updateProperty']),
    getInnerJson: function () {
      return this.$refs.jsonTemplate.$refs.schema_integration_template.innerText
    },
    getAjaxSchemaSettings: function (post_id, security) {
      axios.post('/wp-admin/admin-ajax.php?action=get_schema_settings', {
        'post_id': post_id,
        'security': security,
      })
          .then((response) => {
            if (response.data) {
              this.run(JSON.parse(response.data.schema))
              this.postConditions = JSON.parse(response.data.conditions)
              this.schemaExist = true
              this.setCurrentSchema(JSON.parse(response.data.schema_name))
              this.buildFromConfig(this.getCurrentSchema.title)
              this.setDefaultData({
                key: this.getCurrentSchema.title,
                jsonString: this.default
              })
            }
          })
          .catch((error) => {
            this.setCurrentSchema(this.schemas_type[0])
            const config = this.buildFromConfig(this.getCurrentSchema.title)
            this.setDefaultData({
              key: this.getCurrentSchema.title,
              jsonString: this.default
            })
            this.run(config)
          })
    },
    displayJsonTemplate(haystack, needle, data) {
      if ('undefined' !== typeof needle.value && 'undefined' !== typeof needle.showOnChange) {
        let show = this.findVal(haystack, needle.showOnChange)
        let type = typeof data
        if ('undefined' !== typeof show) {
          if ('object' !== type) {
            show.renderJson = '' !== data
          } else if (data && data.hasOwnProperty('value') && 'object' === typeof data.value) {
            let selected = data.value.find(i => {
              return i.selected
            })
            show.renderJson = 'undefined' !== typeof selected
          } else if (data && data.hasOwnProperty('renderJson')) {
            show.renderJson = data.renderJson
          }
        }
      }
    },
    updateObject(object, key, item) {
      let result = this.findVal(object, key)
      if (undefined !== result) {
        result = this.mergeDeep(result, item)
        this.displayJsonTemplate(object, result, item)
      }
    },
    updateItem(object, key, value) {
      let result = this.findVal(object, key)
      if ('undefined' !== typeof result && result.hasOwnProperty('value')) {
        result.value = value
        this.displayJsonTemplate(object, result, value)
      }
    },
    updateProperties() {
      this.updateProperty(['schemaType', this.getSchemaType])
      this.result = this.getInnerJson()
      this.source = JSON.stringify(this.getSourceType)
    },
    buildFromConfig: function (type) {
      let selected = type
      let defaultConfig = this.config
      let defaultConfigString = JSON.stringify(defaultConfig)
      this.default = defaultConfigString
      const config = JSON.parse(defaultConfigString)
      if ('undefined' !== typeof config[selected]) {
        return config[selected]
      } else {
        return false
      }
    },
    selectSchema: function (event) {
      this.setCurrentSchema(event)
      const config = this.buildFromConfig(this.getCurrentSchema.title)
      this.setDefaultData({
        key: this.getCurrentSchema.title,
        jsonString: this.default
      })
      this.run(config)
      this.itemWalker(this.getSchemaType)
      this.updateProperties()
    },
    itemWalker: function (items) {
      if ('object' === typeof items) {
        Object.entries(items).forEach(([index, item]) => {
          if (undefined !== item.children) {
            this.itemWalker(item.children)
          } else {
            if ('undefined' === typeof this.$options.components[item.formComponent] || false === item.render) {
              delete item[index]
            }
          }
        })
      }
    }
  }
  ,
  computed: {
    ...mapGetters(["getJsonType", "getSchemaType", "getSourceType", "getCurrentSchema", "getSchemasList"]),
    getSelectedSchema: {
      get() {
        return this.getCurrentSchema
      },
      set(value) {
        return this.setCurrentSchema(value)
      }
    },
    getSchemaName: {
      get() {
        return JSON.stringify(this.getCurrentSchema)
      },
      set(value) {
        return this.setCurrentSchema(value)
      }
    },
  },
  updated() {
    this.$nextTick(function () {
      this.result = this.getInnerJson()
    })
  },
  mounted() {
    schemaEmitter.$on('input', (key, value) => {
      if ('object' === typeof value) {
        this.updateObject(this.getSchemaType, key, value)
        this.updateObject(this.getJsonType, key, value)
        this.updateObject(this.getSourceType, key, value)
      } else {
        try {
          value = JSON.parse(value)
        } catch (e) {
        }
        this.updateItem(this.getSchemaType, key, value)
        this.updateItem(this.getJsonType, key, value)
        this.updateItem(this.getSourceType, key, value)
      }
      this.updateProperties()
      this.result = this.getInnerJson()
      this.source = JSON.stringify(this.getSourceType)
    })
    setTimeout(() => {
      this.result = this.getInnerJson()
      this.source = JSON.stringify(this.getSourceType)
    }, 1000);
  },
  created() {
    if ('undefined' !== typeof wp && wp.hasOwnProperty('i18n')) {
      const {__, _x, _n, _nx} = wp.i18n;
    }
    let root = document.getElementById('app')
    if (null !== root) {
      let post_id = root.getAttribute('data-id')
      let security = root.getAttribute('data-security')
      let conditions = root.getAttribute('data-conditions')
      this.conditions = JSON.parse(conditions)

      if (post_id) {
        this.setSchemasList(this.schemas_type)
        this.getAjaxSchemaSettings(post_id, security)
      }
    }
  },
  beforeDestroy() {
    schemaEmitter.$off('input')
  }
}
</script>

<style>
.schema_integration-tag {
  color: #881280;
}

.schema_integration-attr {
  color: #994500;
}

.schema_integration-attr-value {
  color: #1a1aa6;
}

.schema_integration-str {
  background-color: #ffc10780;
  font-weight: 700;
}

.schema_integration-str__updated {
  color: #f44336;
}
</style>

