import functions from "@/mixins/functions";
import {mapActions, mapGetters} from "vuex";

export default {
    mixins: [functions],
    data() {
        return {
            rating: {
                type: Object,
                default() {
                    return {
                        ratingValue: null,
                        ratingCount: null,
                        reviewCount: null,
                        worstResult: null,
                        bestResult: null,
                    }
                }
            }
        }
    },
    methods: {
        ...mapActions(['setRatingResult', 'setWorstResult', 'setBestResult']),
        ...mapGetters(['getRating']),
        calculateRating(field, value) {
            if ('' !== value) {
                value = parseInt(value)
            }
            switch (field) {
                case 'aggregateRating.ratingValue':
                    this.setRating(value)
                    break;
                case 'aggregateRating.worstRating':
                    this.setWorst(value)
                    break;
                case 'aggregateRating.bestRating':
                    this.setBest(value)
                    break;
                default:
                    break;

            }
        },
        setRating(value) {
            this.setRatingResult(value)
        },
        setWorst(value) {
            this.setWorstResult(value)
        },
        setBest(value) {
            this.setBestResult(value)
        }
    },
    mounted() {
        this.rating = this.getRating()
    }
}
