<template>
  <div>
    <v-textarea
        v-model="currentValue"
        :label="componentProps.placeholder"
        @input="updateField(updateKey, $event)"
        @change="validateInput($v)"
        :full-width="fullWidth"
        :counter="counter"
        :counter-value="printCounterValue"
        :error-messages="displayErrors(componentProps.validation, $v.currentValue)"
    ></v-textarea>
  </div>
</template>

<script>

import validation from "@/mixins/validation";
import formProps from "@/mixins/form-props";
import {schemaEmitter} from "@/main";
import functions from "@/mixins/functions";

export default {
  mixins: [validation, formProps, functions],
  props: {
    updateKey: {
      type: [String, Array],
      required: true
    },
  },
  data() {
    return {
      currentValue: '',
      validators: {},
      fullWidth: true
    }
  },
  methods: {
    updateField(field, value) {
      schemaEmitter.$emit('input', field, value)
    }
  },
  validations() {
    return {
      currentValue: this.validators
    }
  },
  mounted() {
    this.validators = this.createValidatorsObject(this.componentProps.validation)
    if( this.componentProps.value ) {
      this.currentValue = JSON.stringify(this.componentProps.value)
    }
  }
}
</script>

