<template>
  <div>
    <div class="repeater-items row" v-for="(item, key) in getCurrentValue" :key="key">
      <form-tree v-if="!componentProps.hide" v-for="(el, k) in item" :index="key" :key="k" :type="k"
          :node="el"></form-tree>
      <v-row>
        <v-col cols="6">
          <v-btn small color="primary" @click="addItem(key, componentProps.name)">
            <v-icon small>
              mdi-plus
            </v-icon>
            {{ componentProps.buttonTitle }}
          </v-btn>
        </v-col>
        <v-col cols="6">
          <v-btn small color="error" @click="deleteItem(key)" :disabled="disableButton">Delete</v-btn>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
import {schemaEmitter} from '@/main'
import {mapActions, mapGetters} from 'vuex'
import formProps from "@/mixins/form-props";
import functions from "@/mixins/functions";
import validation from "@/mixins/validation";

export default {
  mixins: [validation, formProps, functions],
  data() {
    return {
      currentValue: String,
    }
  },
  components: {
    FormTree: () => import("@/FormElements/FormTree")
  },
  methods: {
    ...mapActions(['setReviewRating', 'updateReviewRating']),
    ...mapGetters(['getReviews', 'getRating', 'getDefaultData']),
    addItem(key, name) {
      let properties = {}
      if (this.componentProps.value.length === 1 && this.componentProps.hide) {
        properties['hide'] = false
        if ('review' === this.name || 'recipeInstructions' === this.name) {
          properties['renderJson'] = true
        }
        schemaEmitter.$emit('input', this.name, properties)
      } else {
        let itemObject = JSON.parse(JSON.stringify(this.findVal(this.getDefaultData(), this.componentProps.name)))
        let item = itemObject.value.shift()
        let newItem = this.changeItemKey(item, key, name)
        this.componentProps.value.push(newItem)
        schemaEmitter.$emit('input', this.name, {
          value: this.componentProps.value
        })
      }
      this.updateReviews()
      this.updateBreadcrumbs(this.componentProps.value)
      this.duplicateValues(this.componentProps.value)
    },
    deleteItem(id) {
      let properties = {}
      if (this.componentProps.value.length === 1 && this.componentProps.hide === false) {
        properties['hide'] = true
        if ('review' === this.name || 'recipeInstructions' === this.name) {
          properties['renderJson'] = false
        }
        schemaEmitter.$emit('input', this.name, properties)
        schemaEmitter.$emit('input', 'review.0.reviewRating.ratingValue', '')
        this.setReviewRating({
          0: ""
        })
        this.updateRatingValues('')
      }
      if (this.componentProps.value.length > 1) {
        this.componentProps.value.splice(id, 1)
        schemaEmitter.$emit('input', this.name, {
          value: this.componentProps.value
        })
      }
      this.updateReviews()
      this.updateBreadcrumbs(this.componentProps.value)
      this.duplicateValues(this.componentProps.value)

    },
    changeItemKey(item, key, name) {
      let arrayKey = Object.keys(item).shift().split('.')
      arrayKey.splice(-1, 1)
      let id = arrayKey.join('.')
      let uuid = Math.random().toString(16).slice(2)
      let newKey = `${name}.${uuid}`
      let objectString = JSON.stringify(item)
      let exp = new RegExp(`"${id}`, 'g')
      let newObject = objectString.replace(exp, `"${newKey}`)
      newObject = newObject.replace(/"uuid":"(.+?)",/gm, `"uuid":"${uuid}",`)

      return JSON.parse(newObject)
    },
    updateReviews() {
      if ('review' === this.name && undefined !== this.getCurrentValue) {
        let reviewObject = {}
        this.getCurrentValue.forEach((item, index) => {
          let exp = new RegExp('.reviewRating.ratingValue$', 'g')
          let resultItem = this.findVal(item, exp, true)
          reviewObject[resultItem.uuid] = resultItem.value
        })
        this.setReviewRating(reviewObject)
        let rating = this.getRating()
        schemaEmitter.$emit('input', 'aggregateRating.ratingValue', rating.ratingValue)
        schemaEmitter.$emit('input', 'aggregateRating.ratingCount', rating.ratingCount)
        schemaEmitter.$emit('input', 'aggregateRating.reviewCount', rating.reviewCount)
      }
    },
    updateBreadcrumbs(items) {
      if ('itemListElement' === this.name) {
        items.forEach(function (item, i) {
          let keys = Object.keys(item)
          let positionKey = keys.filter(el => el.endsWith('.position')).shift()
          let index = i + 1
          schemaEmitter.$emit('input', positionKey, index)
        })
      }
    },
  },
  validations() {
    return {
      getCurrentValue: this.validators
    }
  },
  mounted() {
    this.updateReviews()
  },
  computed: {
    validators: {
      get() {
        return this.createValidatorsObject(this.componentProps.validation)
      },
      set(value) {
        return value
      }
    },
    getCurrentValue: {
      get() {
        this.validateInput(this.$v)
        this.duplicateValues(this.componentProps.value)
        return this.componentProps.value
      },
      set(value) {
        return value
      }
    },
    disableButton: function () {
      if (this.componentProps.hasOwnProperty('hide')) {
        return !!(this.componentProps.value.length === 1 && this.componentProps.hide);
      } else {
        return this.componentProps.value.length <= 1;
      }
    }
  }
}
</script>

