export default {
    state: {
        rating: {
            ratingValue: null,
            ratingCount: null,
            reviewCount: null,
            worstResult: null,
            bestResult: null,
        },
        reviews: {}
    },
    mutations: {
        updateReview(state, data) {
            state['reviews'][data.number] = data.value
        },
        setReview(state, data) {
            state['reviews'] = data
        },
        updateRating(state, data) {
            state['rating'] = data
        },
    },
    actions: {
        updateReviewRating({commit, dispatch}, review) {
            commit('updateReview', review)
            dispatch('calculateRating')
        },
        setReviewRating({commit, dispatch}, review) {
            commit('setReview', review)
            dispatch('calculateRating')
        },
        calculateRating({commit, getters}) {
            let currentRating = getters.getRating
            let total = null;
            let result = {
                ratingValue: null,
                ratingCount: null,
                reviewCount: null,
                worstResult: currentRating.worstResult,
                bestResult: currentRating.bestResult,
            }
            let ratingCount = null;
            let reviewRating = getters.getReviews
            let reviewCount = Object.keys(reviewRating).length
            for (let rating in reviewRating) {
                if (reviewRating.hasOwnProperty(rating) && reviewRating[rating] !== "") {
                    ratingCount += 1
                    total += parseInt(reviewRating[rating])
                }
            }
            if (null !== ratingCount) {
                result.ratingCount = ratingCount
            }
            if (null !== total) {
                result.ratingValue = total / ratingCount
            }
            result.reviewCount = reviewCount
            commit('updateRating', result)
        },
        setRatingResult({commit}, value) {
            // commit('updateRating', {
            //     ratingValue: value
            // })
        },
        setBestResult({commit, getters}, value) {
            let currentRating = getters.getRating
            commit('updateRating', {
                ratingValue: currentRating.ratingValue,
                ratingCount: currentRating.ratingCount,
                reviewCount: currentRating.reviewCount,
                bestResult: value,
                worstResult: currentRating.worstResult
            })
        },
        setWorstResult({commit, getters}, value) {
            let currentRating = getters.getRating
            let reviews = getters.getReviews
            commit('updateRating', {
                ratingValue: currentRating.ratingValue,
                ratingCount: currentRating.ratingCount,
                reviewCount: currentRating.reviewCount,
                bestResult: currentRating.bestResult,
                worstResult: value
            })
        },
    },
    getters: {
        getRating(state) {
            return state.rating
        },
        getReviews(state) {
            return state.reviews
        }
    }
}