import Vue from 'vue'
import App from './App.vue'
import vuetify from './plugins/vuetify';
import 'material-design-icons-iconfont/dist/material-design-icons.css'
import store from './store'

Vue.config.productionTip = false

export const schemaEmitter = new Vue()

new Vue({
    vuetify,
    store,
    render: h => h(App),
    created() {
        if ('undefined' !== typeof schema_integration && schema_integration.hasOwnProperty('i18n')) {
            this.setLocaleData(schema_integration.i18n['schema_integration'])
        }
    }
}).$mount('#app')
