<template>
  <span v-if="isDisplayName">
    <span>"{{ name }}"</span>: "<span class="schema_integration-str" :class="{ 'schema_integration-str__updated': isUpdate }">{{ getFilters(value, filters) }}</span>"{{ comma }}
  </span>
  <span v-else>
    "<span class="schema_integration-str" :class="{ 'schema_integration-str__updated': isUpdate }">{{ getFilters(value, filters) }}</span>"{{ comma }}
  </span>
</template>

<script>

import jsonProps from "@/mixins/json-props"
import functions from "@/mixins/functions";

export default {
  mixins: [functions, jsonProps],
  data() {
    return {
      isUpdate: false
    }
  },
  watch: {
    value(item) {
      if( item ) {
        this.isUpdate = item.length > 0
      }
    },
  }
}
</script>

