export default {
    filters: {
        beforeText: function (string, textBefore = '') {
            return `${textBefore}${string}`
        },
        afterText: function (string, textAfter = '') {
            return `${string}${textAfter}`
        },
        toMinutes: function (string) {
            return `PT${string}M`
        },
        clearSymbols: function (string, symbols) {
            symbols.forEach(symbol => {
                string = string.replaceAll(symbol, '')
            })
            return string
        },
        prettyJSON: function (string) {
            if (!string) {
                return ''
            }
            try {
                return JSON.stringify(string, null, 4)
            } catch (e) {
                return ''
            }
        }
    }
}
