export default {
    state: {
        default: {},
        schemaType: {},
        jsonType: {},
        sourceType: {}
    },
    mutations: {
        updateDefault(state, data) {
            state['default'] = data
        },
        updateProperty(state, data) {
            state[data[0]] = data[1]
        },
    },
    actions: {
        setDefaultData: function ({commit}, data) {
            let object = JSON.parse(data.jsonString)
            commit('updateDefault', object[data.key])
        },
        run: function ({commit}, data) {
            if (data) {
                commit('updateProperty', ['schemaType', data])
                commit('updateProperty', ['jsonType', JSON.parse(JSON.stringify(data))])
                commit('updateProperty', ['sourceType', JSON.parse(JSON.stringify(data))])
            }
        }
    },
    getters: {
        getJsonType(state) {
            return state.jsonType;
        },
        getSchemaType(state) {
            return state.schemaType
        },
        getSourceType(state) {
            return state.sourceType
        },
        getDefaultData(state) {
            return state.default
        }
    }
}