<template>
  <div>
    <v-text-field
        v-model="getCurrentValue"
        :label="componentProps.placeholder"
        @input="updateField(updateKey, $event)"
        :counter="counter"
        :counter-value="printCounterValue"
        :error-messages="displayErrors(componentProps.validation, $v.getCurrentValue)"
        v-bind="vuetifyProps"
    ></v-text-field>
  </div>
</template>

<script>

import validation from "@/mixins/validation";
import formProps from "@/mixins/form-props";
import rating from "@/mixins/rating";
import {mapActions, mapGetters} from 'vuex'
import {schemaEmitter} from "@/main";

export default {
  mixins: [validation, formProps, rating],
  props: {
    updateKey: {
      type: [String, Array],
      required: true
    }
  },
  methods: {
    ...mapGetters(['getRating', 'getReviews']),
    ...mapActions(['updateReviewRating']),
    updateField(field, value) {
      let index = field.split('.')[1]
      if (undefined !== index) {
        this.updateReviewRating({
          number: index,
          value: value
        })
      }
      schemaEmitter.$emit('input', field, value)
      this.updateRatingValues(value)
    }
  },
  validations() {
    return {
      getCurrentValue: this.validators
    }
  },
  computed: {
    validators: {
      get() {
        return this.createValidatorsObject(this.componentProps.validation)
      },
      set(value) {
        return value
      }
    },
    getCurrentValue: {
      get() {
        this.validateInput(this.$v)
        return this.componentProps.value ?? parseInt(this.componentProps.value)
      },
      set(value) {
        return value
      }
    }
  }
}
</script>

