<template>
  <div>
    <div
        v-if="node.formComponent === 'Input' || node.formComponent === 'TextArea' || node.formComponent === 'ReviewRating'">
      <single-item :name="name" :value="node.value" :comma="comma"
          :isDisplayName="isDisplayName" :filters="getFiltersProp()"></single-item>
    </div>
    <div v-if="node.formComponent === 'DatePicker'">
      <date-item :name="name" :value="node.value" :comma="comma"></date-item>
    </div>
    <div v-else-if="node.formComponent === 'Selector'">
      <selector-item :name="name" :value="node.value" :filters="getFiltersProp()" :comma="comma"></selector-item>
    </div>
    <div v-else-if="node.formComponent === 'Repeater' && node.repeaterType === 'object'">
      <object-item :name="name" :value="node.value" :comma="comma"></object-item>
    </div>
    <div v-else-if="node.formComponent === 'Repeater' && node.repeaterType === 'array' ">
      <array-item :name="name" :value="node.value"></array-item>
    </div>
    <div v-else-if="node.formComponent === false">
      <span></span><span>"{{ name }}"</span>: <span>{</span>
      <node v-if="renderItem(value)" v-for="(value, key, index) in node.children" :name="value.name"
          :key="key" :type="key" :node="value"
          :comma="printComma(index, node.children)"></node>
      <span>}{{ comma }}</span>
    </div>
  </div>
</template>

<script>

import SingleItem from "./SingleItem";
import ArrayItem from "./ArrayItem";
import SelectorItem from "./SelectorItem";
import DateItem from "./DateItem";
import ObjectItem from "@/JsonElements/ObjectItem";
import functions from "@/mixins/functions";

export default {
  mixins: [functions],
  name: "node",
  props: {
    node: Object,
    type: [String, Number],
    comma: String,
    name: String,
    isDisplayName: {
      type: Boolean,
      required: false,
      default: true
    },
  },
  components: {
    SingleItem,
    ArrayItem,
    SelectorItem,
    DateItem,
    ObjectItem
  },
  methods: {
    getFiltersProp() {
      return this.node.hasOwnProperty('filters') ? this.node.filters : {}
    }
  }
}
</script>

<style scoped>
.node-tree {
  list-style-type: none;
}

div {
  margin-left: 10px;
}
</style>
