<template>
  <div>
    <v-menu
        v-model="menu2"
        :close-on-content-click="false"
        :nudge-right="40"
        transition="scale-transition"
        offset-y
        attach=""
        min-width="290px"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-text-field
            v-model="getCurrentValue"
            :label="componentProps.placeholder"
            prepend-icon="event"
            v-bind="attrs"
            v-on="on"
            :vuetifyProps="vuetifyProps"
        ></v-text-field>
      </template>
      <v-date-picker v-model="getCurrentValue" @change="menu2 = false"
          @input="updateField(updateKey, $event)"></v-date-picker>
    </v-menu>
  </div>
</template>

<script>
import {schemaEmitter} from "@/main";
import formProps from "@/mixins/form-props";
import functions from "@/mixins/functions";

export default {
  mixins: [formProps, functions],
  props: {
    updateKey: {
      type: [String, Array],
      required: true
    }
  },
  data: () => ({
    currentValue: new Date().toISOString().substr(0, 10),
    menu: false,
    modal: false,
    menu2: false,
    validators: {}
  }),
  methods: {
    updateField(field, value) {
      schemaEmitter.$emit('input', field, value)
      this.duplicateValues(value)
    }
  },
  mounted() {
    this.updateField(this.updateKey, this.getCurrentValue)
  },
  computed: {
    getCurrentValue: {
      get() {
        return this.componentProps.value ?? new Date().toISOString().substr(0, 10)
      },
      set(value) {
        return value
      }
    }
  }
}
</script>

