<template>
  <span v-if="isDisplayName">
    <span>"{{ name }}"</span>: "<span class="schema_integration-str">{{ getSelectedItem }}</span>"{{ comma }}
  </span>
  <span v-else>
    "<span class="schema_integration-str">{{ getSelectedItem }}</span>"{{ comma }}
  </span>
</template>

<script>
import jsonProps from "@/mixins/json-props"
import functions from "@/mixins/functions";

export default {
  mixins: [functions, jsonProps],
  computed: {
    getSelectedItem: function () {
      for (let item in this.value) {
        if (this.value[item].selected) {
          return this.getFilters(this.value[item].value, this.filters)
        }
      }
    }
  }
}
</script>

