import {schemaEmitter} from "@/main";
import filters from "@/mixins/filters";

export default {
    mixins: [filters],
    methods: {
        findVal: function (object, key, regexp = false) {
            let value;
            Object.keys(object).some(k => {
                if (regexp && k.search(key) !== -1) {
                    value = object[k];
                    return true;
                } else if (k === key) {
                    value = object[k];
                    return true;
                }
                if (object[k] && typeof object[k] === 'object') {
                    value = this.findVal(object[k], key, regexp);
                    return value !== undefined;
                }
            })

            return value;
        },
        changeChildType: function (string) {
            return string.split('.').pop()
        },
        isLastItem: function (items, objects) {
            for (let item in items) {
                let renderJson = 'undefined' !== typeof objects[items[item]].renderJson ? objects[items[item]].renderJson : true
                if (!renderJson) {
                    continue;
                } else {
                    return false;
                }
            }

            return true
        },
        printComma: function (key, value) {
            let objects = Object.keys(value)
            let currentObjectKey = objects[key]

            let renderJson = 'undefined' !== typeof value[currentObjectKey].renderJson ? value[currentObjectKey].renderJson : true
            let length = objects.length

            if (key === length - 1) {
                return ''
            }
            if (!renderJson) {
                return ''
            }

            let spliceObject = objects.splice(key + 1, objects.length)
            if (spliceObject.length > 0) {
                return this.isLastItem(spliceObject, value) ? '' : ','
            }
            return ','
        },
        renderItem: function (item) {
            if (item.renderJson === undefined) {
                return true
            } else {
                return item.renderJson
            }
        },
        isObject: function (item) {
            return (item && typeof item === 'object' && !Array.isArray(item));
        },
        mergeDeep: function (target, ...sources) {
            if (!sources.length) return target;
            const source = sources.shift();

            if (this.isObject(target) && this.isObject(source)) {
                for (const key in source) {
                    if (source.hasOwnProperty(key) && this.isObject(source[key])) {
                        if (!target[key]) {
                            Object.assign(target, {[key]: {}});
                        } else {
                            target[key] = Object.assign({}, target[key])
                        }
                        this.mergeDeep(target[key], source[key]);
                    } else {
                        Object.assign(target, {[key]: source[key]});
                    }
                }
            }

            return this.mergeDeep(target, ...sources);
        },
        getFilters(string = '', filters = {}) {
            if (string) {
                for (let filter in filters) {
                    if ('beforeText' === filter) {
                        string = this.$options.filters.beforeText(string, filters[filter])
                    }
                    if ('afterText' === filter) {
                        string = this.$options.filters.afterText(string, filters[filter])
                    }
                    if ('toMinutes' === filter) {
                        string = this.$options.filters.toMinutes(string)
                    }
                    if ('clearSymbols' === filter) {
                        string = this.$options.filters.clearSymbols(string, filters[filter])
                    }
                }
            }

            return string
        },
        updateRatingValues(value) {
            let rating = this.getRating()
            let reviewsKeys = Object.keys(this.getReviews())
            let ratingValueObject = {
                value: rating.ratingValue
            }
            let ratingCountObject = {
                value: rating.ratingCount
            }
            reviewsKeys.forEach((key) => {
                schemaEmitter.$emit('input', `review.${key}.reviewRating.bestRating`, rating.bestResult)
                schemaEmitter.$emit('input', `review.${key}.reviewRating.worstRating`, rating.worstResult)
                schemaEmitter.$emit('input', `review.${key}.reviewRating.ratingValue`, {
                    validation: {
                        maxValue: rating.bestResult,
                        minValue: rating.worstResult
                    }
                })
            })
            if (rating.ratingValue) {
                ratingValueObject['vuetifyProps'] = {
                    disabled: true
                }
                ratingCountObject['vuetifyProps'] = {
                    disabled: true
                }
            } else {
                ratingValueObject['vuetifyProps'] = {
                    disabled: false
                }
                ratingCountObject['vuetifyProps'] = {
                    disabled: false
                }
            }
            if (rating.ratingValue >= 1) {
                schemaEmitter.$emit('input', 'aggregateRating', {
                    renderJson: true
                })
            } else {
                schemaEmitter.$emit('input', 'aggregateRating', {
                    renderJson: false
                })
            }
            schemaEmitter.$emit('input', 'aggregateRating.ratingValue', ratingValueObject)
            schemaEmitter.$emit('input', 'aggregateRating.ratingCount', ratingCountObject)
        },
        setEmptyValue(item) {
            let type = typeof item
            if ('string' === type) {
                return ''
            }

            if ('object' === type) {
                let newItem = [];
                item.forEach(el => {
                    let obj = {}
                    obj['selected'] = false
                    obj['value'] = el.value
                    newItem.push(obj)
                })
                return newItem
            }

            return ''
        },
        isNotEmptyValue(item) {
            let type = typeof item

            if ('string' === type) {
                return item.length > 0
            }
            if ('object' === type) {
                let selected = item.findIndex(el => {
                    return el.selected
                })
                return -1 !== selected
            }

            return false
        },
        duplicateValues(value) {
            if (this.componentProps.hasOwnProperty('duplicateFor')) {
                let fields = this.componentProps.duplicateFor
                fields.forEach(field => {
                    if ('string' === typeof value) {
                        schemaEmitter.$emit('input', field, value)
                    } else {
                        schemaEmitter.$emit('input', field, {
                            value: value
                        })
                    }
                })
            }
        }
    }
}
