<template>
  <div>
    <div class="conditions-items">
      <v-row v-for="(item, index) in conditions" :key="index" class="conditions-item">
        <v-col cols="6">
          <v-select
              v-model="item.selected"
              :items="item.data"
              attach
              label="Conditions list"
              @change="getConditionsValue(index, $event)"
          ></v-select>
        </v-col>
        <v-col cols="6">
          <div v-if="item.displayIds">
            <v-text-field
                type="number"
                v-model="item.ids"
                label="Add ID"
                @input="getConditionsValueIds(index, $event)"
            ></v-text-field>
          </div>
        </v-col>
        <v-row>
          <v-col cols="6">
            <v-btn small color="primary" @click="addItem(index)">Add</v-btn>
          </v-col>
          <v-col cols="6">
            <v-btn small color="error" @click="deleteItem(index)" :disabled="disableButton">Delete</v-btn>
          </v-col>
        </v-row>
      </v-row>
    </div>
    <input type="hidden" :name="'conditions'" v-model="result">
  </div>
</template>

<script>

export default {
  props: ['conditionsList', 'currentConditions'],
  data() {
    return {
      conditions: [
        {
          data: [],
          selected: '',
          displayIds: false,
          ids: ''
        }
      ],
      result: ''
    }
  },
  watch: {
    currentConditions: {
      handler: 'setConditions'
    },
    conditions: {
      deep: true,
      handler() {
        this.result = JSON.stringify(this.getConditionsResult())
      }
    }
  },
  methods: {
    addItem(index) {
      let item = JSON.parse(JSON.stringify(this.conditions[index]))
      this.conditions.push(item)
    },
    deleteItem(index) {
      if (this.conditions.length > 1) {
        this.conditions.splice(index, 1)
      }
    },
    getConditionsValue(index, value) {
      this.conditions[index].displayIds = value.startsWith('{custom_')
    },
    getConditionsValueIds(index, value) {
      this.conditions[index].ids = value
    },
    getConditionsResult() {
      let result = []
      this.conditions.forEach(item => {
        let newItem = {
          condition: item.selected,
          custom: item.displayIds ? item.ids : ''
        }
        result.push(newItem)
      })
      return result
    },
    setConditions() {
      if (this.currentConditions.length <= 0) {
        this.conditions[0].data = this.conditionsList
      } else {
        this.conditions.splice(0, 1)
        this.currentConditions.forEach(item => {
          this.conditions.push({
            data: this.conditionsList,
            selected: item.condition,
            ids: item.custom,
            displayIds: item.custom.length > 0
          })
        })
      }
    }
  },
  computed: {
    disableButton: function () {
      return this.conditions.length <= 1;
    }
  },
  created() {
    this.setConditions()
  }
}
</script>

