<template>
  <v-col :class="classes" :cols="cols" v-if="node.render">
    <v-row v-if="node.children && 'object' === typeof node.children">
      <node v-for="(value, key, index) in node.children" :index="index" :key="key" :type="key" :node="value"></node>
    </v-row>
    <div v-else>
      <component
          v-if="node.render"
          :is="node.formComponent"
          :name="type"
          :updateKey="type"
          :vuetifyProps="getCustomProps(node)"
          :componentProps="node"
      ></component>
    </div>
  </v-col>
</template>

<script>
import Input from "@/FormElements/Input";
import Repeater from "@/FormElements/Repeater";
import Selector from "@/FormElements/Selector";
import DatePicker from "@/FormElements/DatePicker";
import TextArea from "@/FormElements/TextArea";
import ReviewRating from "@/FormElements/ReviewRating";
import Custom from "@/FormElements/Custom";
import styles from "@/mixins/styles";

export default {
  mixins: [styles],
  name: "node",
  props: {
    node: Object,
    type: [String, Number],
    index: Number
  },
  components: {
    Input,
    Selector,
    Repeater,
    DatePicker,
    TextArea,
    ReviewRating,
    Custom
  },
  methods: {
    getCustomProps(node) {
      return node.vuetifyProps !== undefined ? node.vuetifyProps : {}
    }
  },
  computed: {
    changeChildType() {
      return this.type.split('.').pop()
    }
  },
  mounted() {
  }
}
</script>

