import Vue from 'vue'
import Vuelidate from "vuelidate";

import {required, maxLength, url, numeric, minValue, maxValue} from 'vuelidate/lib/validators'

const isJson = (string) => {
    try {
        JSON.parse(string)
        return true
    } catch (e) {
        return false
    }
}

Vue.use(Vuelidate)

export default {
    data() {
        return {
            messages: {
                required: 'This field is required',
                maxLength: 'Too long field',
                url: 'Invalid URL',
                minValue: '',
                maxValue: '',
                numeric: 'Number is required',
                isJson: 'The input value not in json format'
            },
            counter: '',
        }
    },
    methods: {
        printCounterValue() {
            if (this.counter !== '') {
                return this.currentValue.length
            }
        },
        displayErrors(validation, field, rating = false) {
            let errorMessages = []
            if (validation !== undefined) {
                for (const [key, value] of Object.entries(validation)) {
                    if (field.$dirty && field.$invalid && !field[key]) {
                        errorMessages.push(this.messages[key])
                    }
                }
            }
            return errorMessages
        },
        validateInput($v) {
            $v.$touch()
            return
        },
        createValidatorsObject(items) {
            let Validators = {}

            if (items === undefined || Object.keys(items).length === 0) {
                return Validators
            }

            if (items.required) {
                Validators['required'] = required
            }

            if (items.maxLength > 0) {
                Validators['maxLength'] = maxLength(items['maxLength'])
            }

            if (items.url) {
                Validators['url'] = url
            }

            if (items.minValue > 0) {
                Validators['minValue'] = minValue(items['minValue'])
            }

            if (items.maxValue > 0) {
                Validators['maxValue'] = maxValue(items['maxValue'])
            }

            if (items.numeric) {
                Validators['numeric'] = numeric
            }

            if (items.isJson) {
                Validators['isJson'] = isJsone
            }

            return Validators
        }
    },
    mounted() {
        if (this.validation !== undefined && this.validation.maxLength !== undefined) {
            this.counter = this.validation.maxLength
        }
    }
}
